/* eslint-disable no-nested-ternary */
// @flow
import React, { memo } from 'react';
import ItemTable from './ItemTable';
import IMAGES from '../../../themes/images';
import ModalSort from './ModalSort';

type Props = {
  dataList: Array<{
    id: number,
    category: string,
    subCategory: string,
    technicalDesc: Object,
    totalCost: string,
    date: string,
    progress: number,
    totalAmountPaid: string,
    totalMoney: string,
    remark: string,
    rating: any,
  }>,
  listPartner: Array<{
    id: number,
    value: string,
    label: string,
  }>,
  listCategory: Array<{
    id: number,
    value: string,
    label: string,
  }>,
  isShowModal: boolean,
  handleSubmitForm: Function,
  handleShowModal: Function,
  handleShowModalRating: Function,
};

const TableConstructionManager = ({
  dataList,
  listPartner,
  listCategory,
  handleSubmitForm,
  isShowModal,
  handleShowModal,
  handleShowModalRating,
}: Props) => {
  const renderListConstruction =
    dataList.length > 0 ? (
      dataList.map((item) => (
        <ItemTable
          itemObj={item}
          key={item.id}
          handleShowModalRating={handleShowModalRating}
        />
      ))
    ) : (
      <div className="table-no-data">Bn cha c d n no.</div>
    );

  return (
    <div className="table-construction">
      <div className="table-header d-flex">
        <div className="t-item t-item1">
          <div
            onClick={() => handleShowModal()}
            className="d-flex align-items-center"
            onKeyDown={() => {}}
            role="button"
            tabIndex={0}
          >
            HNG MC / N V
            <img src={IMAGES.iconUp} alt="" className="ico-up style1" />
          </div>
          {isShowModal && (
            <ModalSort
              listPartner={listPartner}
              listCategory={listCategory}
              handleSubmitForm={handleSubmitForm}
            />
          )}
        </div>
        <div className="t-item t-item2">M T K THUT</div>
        <div className="t-item t-item3">
          D TON
          <img src={IMAGES.iconUp} alt="" className="ico-up style1" />
        </div>
        <div className="t-item t-item4">
          THI GIAN
          <br />
          THI CNG
        </div>
        <div className="t-item t-item5">TIN </div>
        <div className="t-item t-item6">
          S TIN
          <br />
           THANH TON
        </div>
        <div className="t-item t-item7">
          S TIN
          <br />
          CN LI
        </div>
        <div className="t-item t-item8">GHI CH</div>
        <div className="t-item t-item9">PHN HI</div>
      </div>
      <div className="table-body">{renderListConstruction}</div>
    </div>
  );
};

export default memo<Props>(TableConstructionManager);
