// @flow
import React from 'react';
import { Link } from 'react-router-dom';
import IMAGES from 'themes/images';
import ROUTERS from 'constants/router';

const Footer = () => {
  return (
    <footer className="footer">
      <div className=" container-fluid">
        <div className="content-footer row">
          <div className="col-left col-4 align-self-center">
            <div className="logo-footer">
              <Link to={ROUTERS.MAIN_PAGE} title="">
                <img src={IMAGES.logoFooter} alt="Logo" />
              </Link>
            </div>
            <div className="mobile-social">
              <div className="address-title">Social</div>
              <div className="address-name d-flex align-items-center">
                <Link to={ROUTERS.MAIN_PAGE} title="">
                  <img src={IMAGES.facebook} alt="" />
                </Link>
                <Link to={ROUTERS.MAIN_PAGE} title="">
                  <img src={IMAGES.youtube} alt="" />
                </Link>
              </div>
            </div>
            <div className="logo-copyright">
              <Link to={ROUTERS.MAIN_PAGE} title="">
                <img src={IMAGES.logoCopyright} alt="Logo" />
              </Link>
            </div>
          </div>
          <div className="col-right col-8 align-self-center">
            <div className="info-company">
              <h3>CNG TY TNHH INCOR</h3>
              <div className="company-desc">
                Vi hn 100+ nh thu thit k v thi cng ni tht  c i
                ng k s ca Incor kim nh h s nng lc v cht lng thi
                cng, sng lc nhng n v ph hp vi tiu chun cng ty 
                ra. Thng qua nh gi y, Incor nm bt khuynh hng lm vic
                v trnh  ca cc nh thu  c nhng t vn chnh xc cho
                khch hng.
              </div>
              <div className="address">
                <div className="address-title">a ch vn phng</div>
                <div className="address-name">
                   Nng: S 1, ng 123, Phng 4, Qun 5, TP 6
                </div>
              </div>
              <div className="address-company">
                <ul className="d-flex align-items-center">
                  <li>
                    <div className="address-title">in thoi</div>
                    <div className="address-name">0123 456 789</div>
                  </li>
                  <li>
                    <div className="address-title">Email</div>
                    <div className="address-name">info@incor.vn</div>
                  </li>
                  <li className="desktop">
                    <div className="address-title">Social</div>
                    <div className="address-name d-flex align-items-center">
                      <Link to={ROUTERS.MAIN_PAGE} title="">
                        <img src={IMAGES.facebook} alt="" />
                      </Link>
                      <Link to={ROUTERS.MAIN_PAGE} title="">
                        <img src={IMAGES.youtube} alt="" />
                      </Link>
                    </div>
                  </li>
                </ul>
              </div>
              <div className="copyright">
                Copyright Incor. All Rights Reserved.
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
